body {
  margin: 0;
  min-height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}


*:focus {
  outline: none
}


.slick-dots li button:before {
  color: #04c2c9!important;
  font-size: 8px!important;
}